.ui-container {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1000px) {
  .chrono {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 5px
  }
  
  .chrono-finished {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 5px;
    background-color: white;
    border-radius: 25px;
    padding: 2%;
    opacity: 1;
    transition: opacity 2s
    /* display: none */
  }
  
  .restart-btn {
    color: white;
    font-size: large;
    background-color: red;
    border: none;
    padding: 4% 8%;
    margin: 10px 0;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

    &:hover {
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 10px;
      filter: brightness(0.9);
      transform: scale(1.1);
    }
  }
  
  .tutorial {
    position: absolute;
    left: 25px;
    bottom: 25px;
    background-color: white;
    border-radius: 25px;
    padding: 20px;
  }
  
  h1 {
    margin : 0;
    font-size: 1.5rem;
  }

  h2 {
    margin : 0;
    font-size: 1rem;
  }
  
  .speed {
    position: absolute;
    left: 25px;
    bottom: 25px;
    font-size: 2rem;
  }
  
}

@media only screen and (min-width: 1000px) {
  .chrono {
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 5px
  }
  
  .chrono-finished {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 25px 25px 0 0;
    padding: 2%;
    opacity: 1;
    transition: opacity 2s
    /* display: none */
  }
  
  .restart-btn {
    color: white;
    font-size: large;
    background-color: red;
    border: none;
    padding: 4% 8%;
    margin: 10px 0;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

    &:hover {
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 10px;
      filter: brightness(0.9);
      transform: scale(1.1);
    }
  }
  
  .tutorial {
    position: absolute;
    left: 25px;
    bottom: 25px;
    background-color: white;
    border-radius: 25px;
    padding: 10px;
  }
  
  h1 {
    margin : 0
  }
  
  .speed {
    position: absolute;
    left: 25px;
    bottom: 25px;
    font-size: 4rem;
  }
  
}

.leaderboard {
  background-color: white;
  border-radius: 0 0 10px 10px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  overflow-Y: auto;
}

.row {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.row:nth-child(odd) {
  background-color: #00000011;
}